.navbar{
    color:black ;
    position: relative;
    float: right;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}
.fa-bar{
    color:#8E0406;
    margin-right: 2rem;
    font-size: 2rem;
    background: none;
}
.nav-menu{
    color: #EFDCE0;
    background-color: #8E0406;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    right: -100%;
    transition: 850ms;
}
.nav-menu.active {
    right: 0;
    transition: 350ms;
  }
.nav-menu ul{
    list-style: none;
}
.nav-text{
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}
.nav-head-text{
    position: relative;
    top: -30px;
    left: -26px;
    text-decoration: none;
    color:#FF8DA7;
    font-size: 28px;
    align-items: left;
    padding: 0 16px 0px 0px;
    border-bottom: 2px solid white ;
}
.nav-text a{
    position: relative;
    text-decoration: none;
    color:#EFDCE0;
    font-size: 22px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: left;
    padding: 0 6px;
    padding-top: 20px;
    border-radius: 4px;
    left: -10px;
    top: 10px;
}
.nav-text a:hover {
    background-color:#FF3161;
    transition: ease 350ms;
  }
.nav-menu-items {
  width: 100%;
}  
.navbar-toggle {
    color: #FFFFFF;
    height: 80px;
    left: 160px;
    top: 30px;
    display: flex;
    position: relative;
    justify-content: end;
    align-items: center;
  }
span {
    margin-left: 16px;
}  
.vl {
    position: relative;
    border-left: 4px solid #FF033E;
    height: 100%;
    left: -240px;
  }
.small-text{
    position: relative;
    bottom: -140px;    
    left: 13px;
    opacity: 50%;
}